import React from "react";
import { makeStyles } from "@material-ui/core";
import authBg from "./../assets/images/auth/authUserBg.webp";
import clockBg from "./../assets/images/auth/authClockBg.webp";
import clockBgFuzzy from "./../assets/images/auth/authClockBgfuzzy.webp";
import authBgFuzzy from "./../assets/images/auth/authUserBgfuzzy.webp";
const AuthContainer = ({ children, user }) => {
  const useStyles = makeStyles((theme) => ({
    authWrapper: {
      maxWidth: "450px",
      margin: "0 auto",
      paddingTop: 180,
      paddingBottom: 80,
      textAlign: "center",
      position: "relative",
      minHeight: "873px",
      [theme.breakpoints.down(992)]: {
        paddingTop: 140,
        maxWidth: "450px",
        minHeight: "auto",
      },
      "&::before": {
        content: `"" !important`,
        position: "absolute",
        width: 450,
        height: 450,
        backgroundImage: `url(${user ? authBg : clockBg})`,
        top: "30%",
        left: "-130%",
        backgroundSize: "cover",
        [theme.breakpoints.down(992)]: {
          width: 190,
          height: 190,
          top: "45%",
          left: "-38%",
        },
        [theme.breakpoints.down(568)]: {
          display: "none",
        },
      },
      "&::after": {
        content: `"" !important`,
        position: "absolute",
        width: 250,
        height: 250,
        backgroundImage: `url(${user ? authBgFuzzy : clockBgFuzzy})`,
        right: "-85%",
        top: "20%",
        backgroundSize: "cover",
        [theme.breakpoints.down(992)]: {
          width: 90,
          height: 90,
          right: "-13%",
          bottom: -90,
          backgroundRepeat: "no-repeat",
          backgroundPosition: 20,
        },
        [theme.breakpoints.down(568)]: {
          display: "none",
        },
      },
    },
  }));

  const classes = useStyles();
  return <div className={classes.authWrapper}>{children}</div>;
};

export default AuthContainer;
