import React, { useLayoutEffect, useState } from "react";
import {
  Button,
  Container,
  makeStyles,
  CircularProgress,
  Snackbar,
  Typography,
  TextField,
} from "@material-ui/core";
import AuthContainer from "../../../containers/authContainer";
import { useHistory } from "react-router-dom";
import { useFormik } from "formik";
import { Alert } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import cookie from "cookie_js";
import {
  postActivationAccountData,
  setAnswerActivationAccount,
  resendActivationAccountCode,
  setResendedCodeData,
} from "../../../redux/reducers/activation";
import { istance } from "../../../api";
import { getProfileData } from "../../../redux/reducers/myProfile";
const Activation = ({ t }) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const { answerActivationAccountAlert, resended_code_data } = useSelector(
    ({ ActivationAccountReducer }) => ActivationAccountReducer
  );

  const [reload, setReload] = useState(false);

  const emailData = cookie.get("ActivationAccountEmail");

  const formik = useFormik({
    initialValues: {
      email: emailData,
      code: "",
    },
    onSubmit: (values) => {
      setReload(true);
      dispatch(postActivationAccountData(values));
    },
  });

  const firstUpdate = React.useRef(true);
  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerActivationAccountAlert.severity === 200) {
      setTimeout(() => {
        cookie.set(
          "token",
          answerActivationAccountAlert.message.token,
          { path: "/" },
          {
            expires: new Date(
              answerActivationAccountAlert.message.expires * 1000
            ).toUTCString(),
          }
        );
        istance.defaults.headers.Authorization = `Bearer ${answerActivationAccountAlert.message.token}`;
        history.push("/dashboard/main");
        dispatch(getProfileData());
      }, 700);
    }
    setReload(false);
    // eslint-disable-next-line
  }, [answerActivationAccountAlert.severity]);
  const useStyles = makeStyles((theme) => ({
    active: {
      minHeight: "100vh",
      background: "#121212",
      [theme.breakpoints.down(992)]: {
        minHeight: "auto",
      },
    },

    formControl: {
      "& .MuiOutlinedInput-input": {
        background: "transparent",
        boxShadow: "none!important",
        color: "#fff",
      },
      "& .MuiInputLabel-formControl": {
        color: "#B8C0CC !important",
        fontWeight: 300,
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        overflow: "hidden",
        maxWidth: "90%",
        fontSize: 16,
      },

      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#B8C0CC",
      },
    },
  }));

  const classes = useStyles();
  return (
    <section className={classes.active}>
      <Snackbar
        open={answerActivationAccountAlert.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() =>
          dispatch(
            setAnswerActivationAccount({
              ...alert,
              open: false,
              message: answerActivationAccountAlert.message,
            })
          )
        }
      >
        <Alert
          onClose={() =>
            dispatch(setAnswerActivationAccount({ ...alert, open: false }))
          }
          severity={
            answerActivationAccountAlert.severity === 200 ? "success" : "error"
          }
          variant="filled"
        >
          {answerActivationAccountAlert.severity === 200
            ? t("SuccessfulLogin")
            : answerActivationAccountAlert.message &&
              answerActivationAccountAlert.message.messages}
        </Alert>
      </Snackbar>
      <Snackbar
        open={resended_code_data.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() =>
          dispatch(
            setResendedCodeData({
              ...resended_code_data,
              open: false,
              severity: null,
            })
          )
        }
      >
        <Alert
          severity={resended_code_data.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {resended_code_data?.message?.messages}
        </Alert>
      </Snackbar>
      <Container maxWidth="lg">
        <AuthContainer user>
          <Typography variant="h3" gutterBottom component="div">
            {t("ActivationAuthentication")}
          </Typography>
          <Typography
            style={{ maxWidth: 325, margin: "0 auto", lineHeight: ".9" }}
            variant="h5"
            gutterBottom
            component="div"
          >
            {t("activationDescr")}
          </Typography>

          <form onSubmit={formik.handleSubmit} autoComplete="off">
            <span className="label">{t("ActivationCode")}</span>
            <TextField
              label={t("EnterActivationCode")}
              type="text"
              variant="outlined"
              name="code"
              value={formik.values.code}
              className={classes.formControl}
              onChange={formik.handleChange}
              required
            />
            <Button
              style={{ marginTop: 24, height: 56, width: "100%" }}
              color="primary"
              variant="contained"
              size="large"
              type="submit"
            >
              {reload ? <CircularProgress disableShrink /> : t("SignInTo")}
            </Button>
            <Button
              style={{ marginTop: 16, height: 56, width: "100%" }}
              color="secondary"
              variant="contained"
              size="large"
              onClick={() =>
                dispatch(resendActivationAccountCode({ email: emailData }))
              }
            >
              Отправить еще раз
            </Button>
          </form>
        </AuthContainer>
      </Container>
    </section>
  );
};

export default Activation;
